import React from "react"
import {Container, Row, Col} from "react-bootstrap"

export default function ResumeHeader(props){
    return (
        <div>
            <Row>
                <Col sm={2}><b>{props.title}</b></Col>
                <Col>{props.location}</Col>
            </Row>

            <Row>
                <Col sm={2}>{props.company}</Col>
                <Col>{props.date}</Col>
            </Row>
        </div>
        
    )
}